.card {
    position: relative;
    width: 100%;
    height: 520px;
    border-radius: 10px;
    background-color: #fff;
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
    transition: 0.5s;
    color: black;
    text-decoration: none;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    transition: 0.5s;
    z-index: 1;
    border: 1px solid rgb(255, 255, 255);
}

.card:hover {
    -webkit-transform: translateY(-20px);
        -ms-transform: translateY(-20px);
            transform: translateY(-20px);
    -webkit-box-shadow: 0 5px 15px 0 rgb(255, 255, 255);
            box-shadow: 0 5px 15px 0 rgb(255, 255, 255);
}

.card__title {
    margin-bottom: 10px;
    font-style: normal;
    font-weight: normal;
    font-size: 26px;
    line-height: 30px;
}

.card__description {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
}

.card__image-block {
    width: 100%;
    height: 270px;
}

.card__image {
    width: 100%;
    height: 100%;
    -o-object-position: 0 0;
       object-position: 0 0;
    -o-object-fit: cover;
       object-fit: cover;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    cursor: pointer;
    -webkit-transition: 5s;
    -o-transition: 5s;
    transition: 5s;
}

.card__image:hover {
    -o-object-position: 0 100%;
       object-position: 0 100%;
}

.card__content {
    width: 90%;
    margin: auto;
}

.card__github-link {
    opacity: 0;
}

.card__github {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    position: absolute;
    bottom: 10px;
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
    transition: 0.5s;
    cursor: pointer;
    text-decoration: none;
    color: #000;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 18px;
}

.card__github-logo {
    margin-right: 5px;
}

.card__github-logo:hover {
    -webkit-transform: scale(1.2);
        -ms-transform: scale(1.2);
            transform: scale(1.2);
    opacity: 0.7;
}

.card__github-logo:hover+.card__github-link {
    opacity: 1;
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
    transition: 0.5s;
}


